body,
html {
  width: 100%;
  height: 100vh;
  margin: 0;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.App {
  height: 100vh;
  width: 100vw;
}

.swiper-container {
  height: 100%;
  width: 100%;
}

.wrap {
  height: 100vh;
  width: 100vh;
  z-index: 100;
}


/* styles.css */

/* 如果屏幕宽度小于 600px，则应用以下样式 */
@media (max-width: 600px) {
  .nav-items h3 {
    font-size: 14px;
  }
  .nav-items{
    width: 240px;
  }

  .nav {
    padding: 10px !important;
  }

  .MuiSvgIcon-root {
    width: 30px !important;
    height: 30px !important;
  }

  .scroll-wrapper p {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  .nav-items h3 {
    /* font-size: 16px; */
  }
  .nav-items{
    width: 70%;
  }
  .nav {
    padding: 75px !important;
  }

  .scroll-wrapper p {
    font-size: 18px;
  }
}

@media (min-width: 600px) and (max-width: 1200px) {
  .nav-items{
    width: 70%;
  }
  .nav {
    padding: 25px !important;
  }

  .scroll-wrapper p {
    font-size: 48px;
  }
}